.film-container {
    height: 80vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    
    padding-left: 1%;
    
    gap: 1%;
    
}

.film-grid {
    height: 100%;
    width: 70%;
    display: flex;
    padding-top: 2%;
    justify-content: center;
    
    
    
}
.film-list-item {
    
    font-size: .8rem;
}

.film-list-container {
    height: 80vh;
    width: 15vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2%;
    gap: 5%;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
   
    
    
    
}
iframe {
   
    width: 100%;
    height: auto;
    
   
}
.bold {
    font-weight: 700;
}
@media screen and (max-width: 850px) {
    .film-container {
        flex-direction: column;
    }
    .film-list-container {
        width: 95vw;
        height: 15vh;
        flex-direction: row;
    }
    .film-grid {
        height: 65vh;
        width: 100vw;
    }
    iframe {
        width: 90%;
    }

}
@media screen and (max-width: 650px) {
    .film-list-item {
        font-size: 1.9vw;
    }
}